import { Component } from "@angular/core";
import { ViewController } from "ionic-angular";

@Component({
    template: `
        <button ion-button full clear no-margin (click)="action('accept')">Đồng ý</button>
        <button ion-button full clear no-margin border-top (click)="action('unfiend')">Hủy yêu cầu</button>
    `
})
export class AnswerInvitationPopover {
    constructor(public viewCtrl: ViewController) {
        // console.log('viewCtrl', this.viewCtrl);
    }

    action(actionName) {
        this.viewCtrl.data.action(actionName);
        this.viewCtrl.dismiss();
    }
}


